/*== стили обвертки ==*/
.menu {
    position: relative;
    z-index: 10;
}

.menu__item {
    position: relative;
    display: block;
    outline: none;
    margin: 0 0 1.5em;
    line-height: 1;
}

.menu__item-name,
.menu__item-label {
    position: relative;
    display: inline-block;
}

.menu__item-name {
    font-size: 1.25em;
}

.menu__item-label {
    margin: 0 0 0 0.5em;
}

.content {
    position: relative;
    display: flex;
    justify-content: center;
    min-height: 100vh;
    margin: 0 auto;
    padding: 15% 5%;
}

/*== стили для навигации ==*/
.menu--mohe .menu__item {
    font-family: 'Space Mono', sans-serif;
    font-weight: 700;
    display: grid;
    justify-content: center;
    margin: 4em 0;
    text-transform: lowercase;
    grid-template-columns: auto;
}

.menu--mohe .menu__item-name {
    font-size: 1.8em;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    white-space: pre;
    pointer-events: none;
    color: #171754;
    grid-area: 1 / 1 / 2 / 2;
}


.menu--mohe .menu__item-name span {
    display: inline-block;
}

.menu--mohe .menu__item-label {
    font-size: 1.25em;
    line-height: 1.4;
    overflow: hidden;
    margin: 0;
    text-align: center;
    color: transparent;
    -webkit-transition: color 0s 0.3s;
    transition: color 0s 0.3s;
    grid-area: 1 / 1 / 2 / 2;
}

.menu--mohe .menu__item:hover .menu__item-label,
.menu--mohe .menu__item:focus .menu__item-label {
    color: #fff;
}

.menu--mohe .menu__item-label::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: #fff;
    -webkit-transform: translate3d(-100%,0,0) translate3d(-1px,0,0);
            transform: translate3d(-100%,0,0) translate3d(-1px,0,0);
    -webkit-transition: -webkit-transform 0.6s;
    transition: -webkit-transform 0.6s;
    transition: transform 0.6s;
    transition: transform 0.6s, -webkit-transform 0.6s;
    -webkit-transition-timing-function: cubic-bezier(0.7,0,0.3,1);
            transition-timing-function: cubic-bezier(0.7,0,0.3,1);
}

.menu--mohe .menu__item:hover .menu__item-label::before,
.menu--mohe .menu__item:focus .menu__item-label::before {
    -webkit-transform: translate3d(100%,0,0) translate3d(1px,0,0);
            transform: translate3d(100%,0,0) translate3d(1px,0,0);
}

